@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&family=Work+Sans:wght@300&display=swap');

.App {
    height: 100vh;
    display: flex;
    background-image: linear-gradient(to right, #a8d8ff, #69a1ff);
    background-position: center;
    background-size: cover;
}

::-webkit-scrollbar {
    width: 0px;
}

::-webkit-scrollbar-thumb {
    background: rgba(136, 136, 136, 0.281);
}

::-webkit-scrollbar-thumb:hover {
    background: #555;
}